import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { Observable } from "rxjs/Observable";
import 'rxjs/Rx';
@Injectable()
export class HttpService {

  constructor(public http:Http) { }

  sendData(todoData:any){
  	// console.log(todoData);
  	const body = JSON.stringify(todoData);
  	const headers = new Headers();
  	headers.append("Content-Type", "application/json");
  	return this.http.post("https://todo-4176a.firebaseio.com/data.json", body, {headers: headers}).map((response:Response) => response.json());
  
  	
  }

  getData(){
  	return this.http.get("https://todo-4176a.firebaseio.com/data.json").map((res: Response)=> res.json());
  }

  getSingleData(id:any){
    return this.http.get("https://todo-4176a.firebaseio.com/data/"+id+".json").map((res: Response)=> res.json());;
  }
}



